.app-layout-login > .ant-layout .ant-layout-header {
  background: transparent;
  padding: 30px;
  height: auto;
  line-height: normal;
  line-height: initial;
}
.app-layout-login > .ant-layout .ant-layout-content {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -ms-flex-align: center;
      align-items: center;
}
.app-layout-login .app-login-form {
  padding: 40px;
  box-sizing: border-box;
  min-width: 500px;
  min-height: 400px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 4px 4px 2px rgba(0, 0, 0, 0.14);
  margin-right: 144px;
  transition: all 0.2s;
}
@media (max-width: 644px) {
  .app-layout-login .app-login-form {
    min-width: 0;
    min-height: 0;
    margin: auto;
  }
}
.app-layout-login .ant-layout-footer {
  background-color: #fff;
  font-family: 'Lato';
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  padding: 24px 0 0;
}
.app-layout-login .ant-layout-footer .app-login-links {
  padding: 0 80px;
}
.app-layout-login .ant-layout-footer .app-login-links .ant-typography {
  color: #666666;
}
.app-layout-login .ant-layout-footer .app-login-links .ant-typography h1 {
  font-size: 18px;
}
.app-layout-login .ant-layout-footer .app-login-links .ant-typography a {
  color: #666666;
  font-size: 16px;
  line-height: 44px;
}
.app-layout-login .ant-layout-footer .app-login-links .app-login-codeimg {
  position: relative;
  min-height: 88px;
  overflow: hidden;
  padding: 8px 0;
  padding-left: 120px;
  margin-top: 24px;
}
.app-layout-login .ant-layout-footer .app-login-links .app-login-codeimg img {
  position: absolute;
  top: 8px;
  left: 8px;
}
.app-layout-login .ant-layout-footer .app-login-links .app-login-codeimg p {
  margin: 0;
  line-height: 44px;
  font-family: 'PingFangSC';
  font-size: 16px;
  color: #666666;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 644px) {
  .app-layout-login .ant-layout-footer .app-login-links {
    padding: 0 20px 15px;
  }
  .app-layout-login .ant-layout-footer .app-login-links .app-login-codeimg {
    margin-top: 0;
  }
}
.app-layout-login .ant-layout-footer .app-login-record {
  color: #333333;
  font-size: 16px;
  text-align: center;
  min-height: 40px;
  line-height: 40px;
  background-color: #f8f8f9;
}
.app-layout-login .ant-layout-footer .app-login-record a {
  color: #666666;
  font-size: 16px;
  line-height: 44px;
}
.app-layout-login .ant-layout-footer .ant-descriptions-item-label::after {
  display: none;
}
