.ant-pro-basicLayout-content .app-layout-tabs {
  position: absolute;
  top: 52px;
  right: -12px;
  left: -12px;
  bottom: -12px;
}
.ant-pro-basicLayout-content .app-layout-tabs .app-animate-content {
  margin: 10px;
  min-height: calc(100% - 20px);
  background-color: #fff;
}
.ant-pro-basicLayout-content .app-layout-tabs .app-animate-content > div {
  padding: 10px 10px 0;
  background-color: #fff;
}
.ant-pro-basicLayout-content .app-layout-tabs .ant-tabs-tabpane-inactive {
  height: 0 !important;
}
.ant-pro-basicLayout-content .app-layout-tabs > .ant-tabs-bar {
  background: #fff;
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.ant-pro-basicLayout-content .app-layout-tabs > .ant-tabs-content > .ant-tabs-tabpane {
  overflow: auto;
  position: relative;
}
.ant-pro-basicLayout-content .app-layout-tabs .ant-tabs-extra-content {
  display: none;
}
.ant-pro-basicLayout-content .app-layout-tabs .ant-tabs-tab {
  border-top: none !important;
}
.ant-pro-basicLayout-content .app-layout-tabs > .ant-tabs {
  overflow: hidden;
}
.ant-pro-basicLayout-content .app-layout-tabs > .ant-tabs .ant-tabs-right-content,
.ant-pro-basicLayout-content .app-layout-tabs > .ant-tabs .ant-tabs-left-content {
  padding: 0;
}
